* {
    margin: 0;
    padding: 0;
}

:root {
    --white: #FFFFFF;
    --black: #000000;
    --outerBg: #23252C;
    --primaryBg: #1A1B21;
    --h1Color: #FFFFFF;
    --roleColor: #F3BF99;
    --subHeader: #F5F5F5;
    --emailBtn: #374151;
    --linkedinBtn: #5093E2;
    --txtColor1: #F5F5F5;
    --txtColor2: #DCDCDC;
    --footerBg: #161619;
    --iconBg: #918E9B;
    --borderRadius: 6px;
    --fontFamily: 'Inter', sans-serif;
}



body {
    font-family: var(--fontFamily);
}

.outer-layer {
    width: 100%;
    height: 100vh;
    margin-inline: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: var(--black);
    color: #FFF;
}

.inner-layer {
    width: 550px;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: var(--outerBg);
}

@media (max-width:550px) {
    .inner-layer {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background-color: var(--outerBg);
    }

    .outer-layer {
        background-color: var(--outerBg);
    }
}

.DBC {
    width: 317px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    border-radius: 10px;
    background-color: var(--primaryBg);
}

.infoSection {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.infoSection img {
    width: 100%;
    border-start-start-radius: 10px;
    border-start-end-radius: 10px;

}

.infoSection .infoTxt {
    margin-block: 1.5em;
    margin-inline: auto;
    width: 247px;
}

.infoSection .name {
    color: var(--h1Color);
    font-size: 25px;
    line-height: 1.1;
}

.infoSection .role {
    color: var(--roleColor);
    font-size: 12.8px;
    line-height: 1.5;
}

.infoSection .website {
    color: var(--subHeader);
    font-size: 10.24px;
    line-height: 1.5;
}

.infoSection .contactBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-block-start: .6em;
}

.infoSection button {
    width: 115px;
    height: 34px;
    outline: none;
    border: 0;
    font-size: 14px;
    border-radius: var(--borderRadius);
}

.infoSection button.email {
    color: var(--emailBtn);
    margin-inline-end: 15px;
    background-color: var(--white);
}

.infoSection button.linkedin {
    color: var(--white);
    background-color: var(--linkedinBtn);
}

.aboutSection,
.interestSection {
    width: 247px;
    margin-inline: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.interestSection {
    margin-block-end: 1em;
}


.aboutSection .title,
.interestSection .title {
    color: var(--txtColor1);
    font-size: 1em;
    font-weight: 700;
    line-height: 2;

}

.aboutSection p,
.interestSection p {
    color: var(--txtColor2);
    font-size: 10.24px;
}

footer {
    border-end-start-radius: 10px;
    border-end-end-radius: 10px;
    background-color: var(--footerBg);

}

footer .footer-items {
    display: flex;
    justify-content: center;
    align-items: center;
}

footer li {
    font-size: 25px;
    list-style: none;
    color: var(--iconBg);
    margin: 10px 12px;
}